#whatwedocontainer {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;

  /* background-color: #ca9797; */
}

#weheading {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10vh 0vw;
}
#weheading > span:nth-child(1) {
  color: #282828;
  font-size: 5rem;
  font-family: "newyorkregular";
  color: #0077b6;
  font-weight: 600;
}
#weheading > span:nth-child(2) {
  font-size: 3rem;
  font-family: "Quicksand", sans-serif;

  margin-left: 1vw;
  margin-top: 1.5vw;
}

#works {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  align-items: center;
  justify-content: center;
  /* background-color: blue; */
}

#works > div {
  transition: 0.3s;
  background-position: center;
  background-size: 390px;
  background-repeat: no-repeat;
  border-radius: 10px;
  margin: 3vh 1vw;

  filter: brightness(100%) contrast(100%) saturate(0%) blur(0px) hue-rotate(0deg);

  /* background-color: aquamarine; */
}
#works > div:hover {
  background-size: 370px;
}

#works > div:nth-child(1) {
  background-image: url("./images/bg2.jpg");
  /* top: 87vh;  */

}
#works > div:nth-child(2) {
  background-image: url("./images/bg1.jpg");
}
#works > div:nth-child(3) {
  background-image: url("./images/bg3.jpg");
  /* top: 87vh;  */

}
#works > div:nth-child(4) {
  background-image: url("./images/img9.jpg");
  /* top: 87vh;  */

}
#works > div:nth-child(5) {
  background-image: url("./images/bg5.jpg");
}
#works > div:nth-child(6) {
  background-image: url("./images/bg4.jpg");
  /* top: 87vh;  */

}
#works > div > span {
  border-radius: 10px;
  font-family: "hk_novamedium";
  color: white;
  font-size: 1.3rem;
  display: grid;
  place-items: center;
  padding: 17vh 6vw;
  background-color: #00000045;
}
#bg2{
  width: 3vw;
  position: absolute;
}



@media only screen and (max-width: 415px) {
 #weheading {
  width: 93%;
  transform: translateY(0vh);
  margin: 5vh 0vw;
}
  #weheading > span:nth-child(1) {
    font-size: 3rem;
  }
  #works > div:nth-child(1) {
    top: 0vh;
}
  #works > div:nth-child(3) {
    top: 0vh;
}
  #works > div:nth-child(4) {
    top: 0vh;
}
  #works > div:nth-child(6) {
    top: 0vh;
}
#works > div {
  height: 22vh;
  width: 39%;
  padding: 0vh 1vw;
  margin: 2vh 3vw;
  background-size: 250px;
}
#works > div:hover{
  background-size: 230px;
}
#substituteheading {
  width: 86%;
  color: #0077b6;
  /* transform: translateY(-15vh); */
  font-size: 3.5rem;
  margin-bottom: 5vh;
  font-family: "newyorkregular";
}
#works > div > span{
  padding: 0;
height: 100%;
width: 100%;
display: grid;
place-items: center;
text-align: center;
}

}





