#whowearecontainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  margin-top: 0vh;
  padding-left: 9vw;
  padding-right: 9vw;
  padding-top: 0vh;
  /* background-color: aquamarine; */
}

#whowearecontainer > div:nth-child(2) {
  /* height: 300vh; */
  /* width: 8vw;
  transform: translateY(30vh); */
}

#substituteheading {
  width: 0%;
  color: green;
  white-space: nowrap;
  /* transform: translateY(-15vh); */
  overflow: hidden;
  font-size: 3.5rem;
  margin-bottom: 5vh;
  font-family: "newyorkregular";
}

#svgcontainer{
  position: relative;
  height: 16vh;
  width: 100vw;
  margin-bottom: 8vh;
}

#leftside {
  top: -13vh;
  position: absolute;
  height: 36vh;
  width: 61vw;
  left: -733px;
  /* transition: 0.1s; */
  /* font-family: "hk_novamedium"; */
  font-family: 'newyorkregular';


  font-size: 8rem;
  /* transform: translateX(-780px); */
}
#leftside > text {
  transition: 0.2s;
  fill: transparent;
  stroke: #0077b6;
  stroke-width: 1px;
}
#rightside {
    /* transition: 0.1s; */
  top: -13vh;
  right: 0;
  position: absolute;
  height: 36vh;
  width: 61vw;
  right: -806px;
  /* font-family: "hk_novamedium"; */
  font-size: 8rem;
  font-family: 'newyorkregular';

  /* transform: translateX(780px); */
}
#rightside > text {
  transition: 0.2s;
  fill: transparent;
  stroke: #0077b6;
  stroke-width: 1px;
}

.svgtrue{
    animation: svg 0.4s ease-in-out forwards;
}
@keyframes svg {
    0%{
        opacity: 0;
    }    
    100%{
        opacity: 1;
    }
}
#profilecard{
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}

#profile{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    background-color: #f1f7ff;
    padding-left: 27vw;
    padding-right: 18vw;
    padding-top: 5vh;
    padding-bottom: 5vh;
    border-radius: 8px;
}
#profile > img{
  border-radius: 50%;
  width: 15vw;
}
#profile > div{
  display: flex;
  flex-direction: column;
  transform: translateX(3vw);
  
}
#profile > div > span:nth-child(1){
  font-family: "hk_novamedium";
  font-size: 1rem;
  color: grey;
}
#profile > div > span:nth-child(2){
  font-family: "hk_novamedium";
  font-size: 1.5rem;
  border-bottom: 1px solid rgb(135, 135, 135);
  height: 8vh;
  width: 2vw;
  /* background-color: #fffcf8; */
  display: grid;
  place-items: baseline;
  white-space: nowrap;
}


ul {
  display: flex;
  transform: translate(-3.5vw, 0%);
  animation: founder 0.6s 4.4s ease-in-out forwards;
  opacity: 1;
}


ul > li  {
  list-style: none;
}

ul> li >a {
  width: 25px;
  height: 25px;
  text-align: center;
  line-height: 24px;
  font-size: 16px;
  margin: 0px 6px;
  display: block;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

ul>li> a .icon {
  position: relative;
  color: #7a7a7a;
  transition: .5s;
  z-index: 3;
}

ul> li> a:hover> .icon {
  color: #fff;
}

ul> li> a:before {
  content: "";
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  background: #f00;
  transition: .5s;
  z-index: 2;
}

ul> li >a:hover:before {
  top: 0;
}

ul> li:nth-child(1)> a:before{
  background: #3b5999;
}

ul> li:nth-child(2)> a:before{
 background:linear-gradient(45deg, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d)
}


ul> li:nth-child(3)> a:before{
  background: #0A66C2;
}

#bio{
  display: flex;
  align-items: center;
  justify-content: center;
  /* background-color: #fffbfb; */
}

#logos{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  transform: translate(-1vw, 8vh);
}
#npc{
  width: 19vw;
  margin-bottom: 5vh;
}
#bni{
  width: 14vw;
}
#description{
  color: #303030;
  font-size: 1rem;
  transform: translateY(-8vh);
  line-height: 31px;
  font-family: 'hk_novamedium';
}



@media only screen and (max-width: 415px) {


  #profile {
      align-items: flex-start;
      flex-direction: column;
      padding-left: 5vw;
    padding-right: 41vw;

    padding-top: 4vh;
    padding-bottom: 2vh;

  }
#profile > img {
    width: 45vw;
    margin-bottom: 4vh;
}
#profile > div {
  transform: translateX(0vw);
}
#profile > div > span:nth-child(2) {
  width: 13vw;
}
ul {
  padding: 0vh 0vw;
  transform: translateX(0vw);
}
#bio {
  flex-direction: column-reverse;
  margin-top: 5vh;
}
#description {
  transform: translateY(0vh);

  padding: 0vh 3vw;
text-align: justify;
}
#logos {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  transform: translate(0vw, 0vh);
}
#npc {
  width: 61vw;
  margin-bottom: 2vh;
  margin-top: 2vh;
}
#bni {
  width: 44vw;
}
#substituteheading {
  width: 100%;
  color: #0077b6;
}

#whowearecontainer > div:nth-child(2) {
  height: 0vh;
  overflow: hidden;
  width: 100vw;
  margin-bottom: 0vh;
}

    
}
