#slideshowcontainer{
    position: relative;
    display: flex;
    justify-content: space-evenly;
    padding: 6vh 2vw;
    width:100vw ;
    background-color: #f1f7ff;
}

#slideshow{
    transition: 0.7s;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 9px;
    height: 32vw;
}
#slideshowtext{
    display: flex;
    /* margin-left: 2vw; */
    align-items: flex-start;
    justify-content: center;
    padding: 0vh 6vw;
    flex-direction: column;
}
#slideshowtext > span:nth-child(1){
    font-size: 1.7rem;
    font-family: 'hk_novamedium';
    margin-bottom: 2vh;
}
#slideshowtext > span:nth-child(2){
    font-size: 0.9rem;
    font-family: 'hk_novamedium';
    line-height: 31px;
    color: grey;
    text-align: justify;
}

#upside{
    position: absolute;
    top: -8vh;
    /* z-index: ; */
}
#downside{
    position: absolute;
    bottom: -10vh;
    transform: rotate(180deg);
}


@media only screen and (max-width: 415px) {
    #slideshowcontainer {
        flex-direction: column-reverse;
        transform: translateX(0vw);
        width: 100vw;
        height: 54vh;
        padding: 5vh 0vw;
    }
    #slideshow {
        border-radius: 0px;
        object-fit: cover;
        width: 90vw;
        height: 27vh;
        margin-left: 5vw;
        border-radius: 10px;
    }
    #slideshowtext {
        display: flex;
        /* background-color: aquamarine; */
        transform: translate(0vw,0vh);
        margin-left: 0vw;
        align-items: flex-start;
        justify-content: space-evenly;
        flex-direction: column;
        width: 88%;
        padding: 0vh 6vw;
        margin-bottom: 3vh;
    }
    #slideshowtext > span:nth-child(2) {
        font-size: 0.8rem;
        font-family: 'hk_novamedium';
        line-height: 22px;
        color: grey;
        padding: 0vh 1vw;
    }
    #slideshowtext > span:nth-child(1) {
        margin-bottom: 3vh;
    }
}