#countcontainer{
    display:flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    height: 100vh;
    width: 100vw;
    /* background-color: #ffffff; */
}
#countcontainer > div{
    left: 8vw;
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 57%;
    width: 31vw;
    font-size: 280px;
  font-family: inherit;
  font-weight: 100;
}
#countcontainer > div > span:nth-child(1){
    height: 100%;
    display: grid;
    width: 50%;
    color: rgb(42, 42, 42);
    /* background-color: aquamarine; */
    place-items: center;
}
#countcontainer > div > div:nth-child(2){
    transition: 0.8s;
    height: 100%;
    width: 50%;
    /* background-color: blue; */
}
#countcontainer > div > div:nth-child(2) > span{
    color: rgb(42, 42, 42);
}
#countcontainer > div > div:nth-child(2) > span:nth-child(1){
    position: absolute;
    height: 100%;
    top: 0;
}
#countcontainer > div > div:nth-child(2) > span:nth-child(2){
    position: absolute;
    height: 100%;
    top: 100%;
}
#countcontainer > div > div:nth-child(2) > span:nth-child(3){
    height: 100%;
    position: absolute;
    top: 200%;
}
#countcontainer > div > div:nth-child(2) > span:nth-child(4){
    height: 100%;
    position: absolute;
    top: 300%;
}
#countcontainer > div > div:nth-child(2) > span:nth-child(5){
    height: 100%;
    position: absolute;
    top: 400%;
}


@media only screen and (max-width: 415px) {
    #countcontainer {
        height: 0vh;
        overflow: hidden;
    }
    #countcontainer > div {
        width: 84vw;
        height: 66%;
    }
}
