#homecontainer{
  position: relative;
  overflow: hidden;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url('./images/pexels-max-rahubovskiy-6969866.jpg');
  background-size: 1278px;
  display: flex;
  align-items: center;
  justify-content: center;
    flex-direction: column;

    
    background-color: rgb(0, 0, 0);
  }



#homecontainer > div{
    display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
    perspective: 200px;
    text-align: center;
  font-family: 'newyorkregular';
  z-index: 1;
  
  padding: 18vh 158px;

  color: #fcfcfc;
  font-size: 10rem;
  background-color: #0000007d;
}
#homecontainer > div > span:nth-child(1){
  /* transition-duration: 0.2s; */
  transition-timing-function: ease;
  position: relative;

}
#homecontainer > div > span:nth-child(2){
  /* transition-duration: 0.2s; */
  transition-timing-function: ease-out;
  /* transition-duration: 0.2s; */
  transition-timing-function: ease;
  position: relative;

}








#scroll-down-animation {
  position: absolute;
  top: 91%;
  right: 4vw;
  z-index: 50;
  transform: translate(-50%,-50%);
}

.mouse {
  margin: 0 auto;
  display: block;
  /* border-radius: 50px; */
  border-right: 2px solid #ffffff;
  height: 55px;
  width: 21px;
  position: relative;
}

.move {
  position: absolute;
  background-color: #ffffff;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  left: 50%;
  transform: translate(-55%, 5px);
  animation: move 2s linear infinite;
}
#scrollheading{
  position: absolute;
  color: white;
  font-size: 0.9rem;
  top: -5px;
  font-weight: bolder;
  left: 30px;
  font-family: 'Quicksand', sans-serif;
}
#logo{
  background-image: url('./images/logo.jpeg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 75px;
  height: 75px;
  position: absolute;

  top: 4vh;
  z-index: 100;
  border: 2px solid #0077b6;
  font-size: 1.1rem;
  transform: translateX(3vw);
  border-radius: 50%;
}

@keyframes move {
  0% {
    transform: translate(-55%, 0px);
    opacity: 0;
  }

  50%{
    transform: translate(-55%,25px);
    opacity: 1;
  }

  100% {
    transform: translate(-55%,40px);
    opacity: 0;
  }
}




@media only screen and (max-width: 415px) {
  #homecontainer > div {
    font-size: 4.2rem;
    padding: 36vh 158px;
}
}