#bg{
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0vw;
    opacity: 1;
    z-index: -1;
    overflow: hidden;
    position: fixed;
    transition-delay: 0.3s;
    color: white;
    font-size: 2.1rem;
    font-family: 'Montserrat';
    animation: bg 5.5s 1s ease-in-out forwards 1;

}

@keyframes bg {

    15%{
        opacity: 1;
        z-index: 100;
    }
85%{
        opacity: 1;
        z-index: 100;
    }
    
    100%{
        opacity: 0;
        z-index: -1;

    }
    
}

#bg > div{
    z-index: 0;
    overflow: hidden;
height: 100%;
width: 10vw;
}
#bg > div > div{
    transform: translateX(-100%);
    height: 100%;
    width: 100%;
    animation: try 4.5s 1s forwards ease-in-out 1;
}
@keyframes try {
    0%{
        background-color: #0077b6;
        transform: translateX(-100%);
    }
    15%{
        background-color: #0077b6;
        transform: translateX(0%);
    }
    85%{
        background-color: #0077b6;
        transform: translateX(0%);
    }
    100%{
        background-color: #0077b6;
        transform: translateX(100%);
    }
}



#heading{
    position: absolute;
    font-size: 6rem;
    /* font-family: 'hk_novamedium'; */
  font-family: 'newyorkregular';

    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center
 ;
}
#heading > span{
    animation: text 2s ease-in-out forwards;
    opacity: 0;
    color: #ffffff;
}
@keyframes text {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
    
}








