@import url('https://fonts.googleapis.com/css2?family=Nanum+Myeongjo&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nanum+Myeongjo:wght@700&display=swap');

#contactwrapper{
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border-radius: 8px;
  padding: 10vh 0vh;
}

#part1{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  font-size: 2.9rem;
  transition-duration: 0.7s;
  /* font-family: 'Nanum Myeongjo', serif; */
  font-family: 'newyorkregular';
  padding: 10vh 4vw;
  /* letter-spacing: 3px; */
  color: rgb(255, 255, 255);
  /* font-family: 'Raleway'; */
  border-radius: 8px 0px 0px 8px;

  background-color: #000000;
}

#part1> span{
  margin: 19vh 0vw;
}



  #addresscontainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    position: relative;
    background-color: #52525200;
    top: 11%;
}

#addresscontainer > div{
  font-family: 'hk_novamedium';


 display: flex;
 align-items: flex-start;
 justify-content: space-evenly;
 flex-direction: column;
  font-size: 0.8rem;
  margin: 0vh 3vw;
  color: #727272;
  background-color: rgba(0, 0, 0, 0);
} 
#addresscontainer > div > span:nth-child(1){
  margin-bottom: 1vh;
  color: white;
} 

#emailanchor{
  transition: 0.4s;
  text-decoration: none;
  color: #727272;
}

#emailanchor:hover{
  text-decoration:underline ;
  color:#00b5ff;
}

#addresscontainer > div:nth-child(1) > span:nth-child(2){
  text-align: start;
  position: relative;
  bottom: 6%;
}
#addresscontainer > div:nth-child(2) > span:nth-child(1){
  position: relative;
  top: 3%;
}
#addresscontainer > div:nth-child(2) > span:nth-child(2){
  position: relative;
  top: 0%;
}
#addresscontainer > div:nth-child(2) > span:nth-child(3){
  position: relative;
  bottom: 17%;
}



#part1 > button{
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 0.9rem;
  font-family: 'Raleway';
  color: white;
  width: 37%;
  font-weight: 600;
  position: relative;
    bottom: 32%;
    overflow: hidden;
    left: 20%;
  border-radius: 4px;
  height: 33px;
  border: none;
  background-color: #323232;
}

#part1 > button > img{
  width: 14%;
}

#part1 > button:hover{
  background-color: #ffffff;
  color: #000000;
}

#mapspan{
  height: 100%;
  width: 72%;
  position: relative;
  transition: 0.2s;
  top: 63%;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translate(0%, 0%);
  flex-direction: column
}
#mapspan > span{
  margin-bottom: 8%
}

@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap');

#part2{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transition: 0.6s;
  font-size: 2.3rem;

  font-family: 'Playfair Display', serif;
/* font-family: 'Raleway'; */
    /* letter-spacing: 3px; */
    color: rgb(0, 0, 0);

  background-color: #ffffff;
}

  #part2 > span{
    background-color: #7fffd400;
    width: 9%;
    white-space: nowrap;
    height: 10%;
    border-bottom: 4px solid pink;
  }


  #cform {
    padding: 0px 4vw;
    display: flex;
    flex-direction: column;
    max-width: 400px;
    margin: 0 auto;
    border: 1px solid #ccc;
  border-radius: 0px 8px 8px 0px;

  }
  
  #cform > h1 {
    background-color: #7fffd400;
    width: 9%;
    font-size: 2.1rem;
    font-weight: 500;
    margin: 6.7vh 0vw;
    white-space: nowrap;
    font-family: 'Montserrat', serif;
    /* font-family: 'hk_novamedium'; */
    height: 10%;
    border-bottom: 4px solid pink;
  }
  
  #cform > label {
    color: grey;
    font-size: 0.8rem;
  font-family: 'hk_novamedium';

  }
  #cform > input[type="text"],
  #cform > input[type="email"],
  #cform > input[type="password"],
  #cform > input[type="number"],
  select {
    padding: 1vh 0vw;
    border-bottom: 1px solid #ccc;
    border-left: none;
    border-right: none;
    border-top: none;
    font-size: 1rem;
    margin-bottom: 2vh;
    font-family: 'hk_novamedium';
    font-size: 0.9rem;
  }
  #cform > textarea{
    border: 1px solid #ccc;
    margin-bottom: 3vh;
    
  }

  
  #cform > input[type="text"]:focus,
  #cform > input[type="email"]:focus,
  #cform > input[type="password"]:focus,
  #cform > input[type="number"]:focus,
  select:focus {
    outline: none;
    transition: 0.3s;
    border-color: cornflowerblue;
  }
  
  
  #submitbtn {
    
    font-size: 13px;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    font-weight: 500;
    margin-right: 19vw;
    padding: 2vh 0vw;
    background-color: transparent;
    color: black;
    border: 1px solid #cdcdcd;
    border-radius: 5px;
    font-family: 'hk_novamedium';
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease 0s;
    cursor: pointer;
    outline: none;

  }
  
  #submitbtn:hover {
    background-color: #0077b6;
    box-shadow: 0px 15px 20px #0076b63d;
    color: #fff;
    transform: translateY(-7px);
  }
  
  #submitbtn:active {
    transform: translateY(-1px);
  }

.warning{
  transition: 0.3s;
  position: absolute;
  opacity: 0;
  width: 6%;
}

#warning1{top: 38%;left: 42%;}
#warning2{top: 38%;left: 82%;}
#warning3{top: 52%;left: 42%;}
#warning4{top: 52%;left: 82%;}
#warning5{top: 65%;left: 42%;}


   #googlemap{
    transition-duration: 0.6s;
    transition-delay: 0.2s ;
    z-index: -1;
    opacity: 0;
    transform: translate(0%, 20%);
    height: 100%;
    width: 50%;
    background-color: #23456700;
    position: absolute;
    right: 0%;

   }


   
@media only screen and (max-width: 415px) {
  #contactwrapper {
    transform: translateY(11vh);
    flex-direction: column;
}
#part1{
  padding: 10vh 11vw;
  font-size: 2.5rem;
  border-radius: 8px;
}

#part1> span {
  margin: 0vh 0vw;
}
#addresscontainer {
  align-items: baseline;
  flex-direction: column;
  margin-top: 11vh;
}
#addresscontainer > div {
  margin: 2vh 0vw;
}
#cform {
  padding: 0px 6vw;
  border-radius: 8px;

  width: 80vw;
  margin: 4vh auto;
}
#cform > h1 {
  font-size: 1.5rem;
  margin: 5vh 0vw;
}
#submitbtn{
  margin-right: 46vw;
}

}