  .cpmnyintro{
    display: flex;
    align-items: center;
    justify-content: space-evenly;

    margin: 8vh 0vw;

  }
  .cpmnyintro > span{
    position: relative;
    text-align: left;
    /* font-family: 'Raleway', sans-serif; */
    font-family: 'hk_novamedium';
    font-size: 1rem;
    line-height: 180%;
    line-height: 44px;
    /* color: #4e4e4e; */
  }
  .cpmnyintro > img{
    border-radius: 6px;
  }
   
  #cpmnyintro1 > span{
    top: 54vh;
    right: 1vw;
    width: 61vw;
  }
   
  #cpmnyintro2 > span{
    left: 3vw;
    top: 69vh;
    width: 61vw;
  }
  
  #cpmnyintro3 > span{
    top: 84vh;
  }
  #cpmnyintro3{
    width: 81%;
  }

  #cpmnyintro1 > img:nth-child(2){
    left: 3vw;
      width: 16vw;
      position: relative;
    }
    
    #cpmnyintro2 > img:nth-child(1){
      position: relative;
      right: 2vw;
      width: 16vw;
    }
    
    #cpmnyintro3 > img:nth-child(2){
      position: relative;
      left: 3vw;
      bottom: 2vh;

      width: 16vw;
  }

  #sparkle{
    width: 1.3vw;
    transform: translate(-6px, 1px);
    
  }


  

  
  #img2{
    width: 18vw;
    /* position: absolute;
    top: 258vh;
    left: 8vw; */
  }
  
  #img3{
    width: 18vw;
    /* position: absolute;
    top: 388vh;
    right: 6vw; */
  }
  

  @media only screen and (max-width: 415px) {
    #aboutuscontainer {
      margin-top: 10vh;
    }
    .cpmnyintro{
      margin: 4vh 0vw;
  }
  #aboutheading{
    margin-bottom: 2vh;
  }
  #aboutheading > span:nth-child(2) {
    font-size: 4rem;
}
  
  #cpmnyintro1 > span {
    top: 30vh;
    left: 0vw;
    width: 92vw;
}
#cpmnyintro3{
  width: 100%;
}

  .cpmnyintro > span {
    line-height: 28px;
    text-align: center;
    font-size: 13px;
}
#cpmnyintro1 > img:nth-child(2) {
    width: 0vw;
}
#cpmnyintro2 > img:nth-child(1) {
  width: 0vw;
}
#cpmnyintro3 > img:nth-child(2) {
  width: 0vw;
}

#cpmnyintro2 > span {
  left: 0vw;
  width: 92vw;
  top: 33vh;
}
#cpmnyintro3 > span {
  top: 33vh;
  left: 0vw;
  width: 92vw;
}
#sparkle {
  width: 4.3vw;
  transform: translate(-7px, 3px);
}
