.gallery{
  transition-duration: 0.5s;
  z-index: 5;
  top: 0vh;
  z-index: 5;
  position: absolute;
  overflow-x: hidden;
  overflow-y: scroll;
  /* left: 92vw; */
  height: 100vh;
  transform: translate(10%, 0%);
  width: 89vw;
  }
  .gallery > div:nth-child(1){
    transform: translateY(3vh);
  }
  .gallery > div:nth-child(2){
    transform: translateY(-3vh);
  }
  .gallery > div:nth-child(3){
    transform: translateY(3vh);
  }
  .gallery > div:nth-child(4){
    transform: translateY(-3vh);
  }
  
  
