  .close {
    position: fixed;
    top: 7%;
    left: -0.6vw;
    width: 50px;
    transform: translate(80%, 0%);
    height: 50px;
    border-radius: 30%;
    z-index: 42;
    transition: 0.4s;
  }

  .close:before, .close:after {
    position: absolute;
    left: 50%;
    content: ' ';
    height: 33px;
    top: 9px;
    width: 2px;
    background-color: rgb(44, 44, 44);
  }

  .close:before {
    transform: rotate(45deg);
  }
  .close:after {
    transform: rotate(-45deg);
  }
  