#clientcontainer{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 15vh;
    margin-bottom: 10vh;


}
#clientheading{
    font-size: 3rem;
    font-family: 'newyorkregular';
    text-rendering: optimizeLegibility;
    color: #0077b6;
    font-weight: 600;
    margin-bottom: 3vh;

}
#clientcontainer  > div:nth-child(2){
    display: flex;
    align-items: center;
    justify-content: center;
}
#clientcontainer  > div:nth-child(2) > img{
    width: 12vw;
    margin: 0vh 2vw;
}


@media only screen and (max-width: 415px) {
    #clientcontainer  > div:nth-child(2) > img{
        width: 24vw;
        margin: 0vh 2vw;
    }

    #clientcontainer > div:nth-child(2) {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }
}
