#testimonialcontainer{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    width: 100vw;
    background-color: #ffffff;
    margin-top: 21vh;
}


#theading{
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgb(52, 52, 52);
    margin: 12vh 0vw;
  }

  #theading > span:nth-child(1){
    font-family: 'Dancing Script', cursive;
    font-size: 5.2rem;
  }

  #theading > span:nth-child(2){
    font-family: 'Quicksand', sans-serif;
    font-size: 2rem;
    margin-left: 1vw;
    margin-top: 5vh;
  }

  #tcard{
    padding: 0vh 2vw;
    color: black;
   display: flex;
   align-items: center;
   justify-content: space-evenly;
   margin-bottom: 19vh;

  }
  .cardchildt{
    border-radius: 7px;
    display: flex;
    padding: 6vh 2vw;
    margin: 0vh 1vw;
    background-color: rgb(255, 255, 255);
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    align-items: flex-start;
    flex-direction: column;
  }
  .cardchildt > img{
width: 3.8vw;
transform: rotate(180deg);
  }
  .cardchildt > span{
    color: #474747;
    letter-spacing: 0.025rem;
    font-family: 'Montserrat', sans-serif;
    font-size: 0.9rem;
    line-height: 1.875;
    font-weight: 400;

    margin: 5vh 0vw;

  }
  .name{
    display: flex;
    flex-direction: column;
  }
  .name > span:nth-child(1){
    font-family: 'Dancing Script', cursive;
    font-size: 1.4rem;
    color: #0077b6;
  }
  .name > span:nth-child(2){
    font-family: 'hk_novamedium';
font-size: 0.9rem;
color: grey;
  }

  
  @media only screen and (max-width: 415px) {
    #testimonialcontainer{
      color: black;
      margin-top: 0vh;
      margin-bottom: -17vh;

  }
  #theading {
    margin: 7vh 0vw;
}


  #theading > span:nth-child(1) {
    font-family: 'Dancing Script', cursive;
    font-size: 4.2rem;
}
#theading > span:nth-child(2) {
  font-family: 'Quicksand', sans-serif;
  font-size: 1.5rem;
  margin-left: 1vw;
  margin-top: 3vh;
}
#tcard {
  flex-direction: column;
  padding: 0vh 4vw;
}
.cardchildt > img {
  width: 14vw;
}

.cardchildt {
  padding: 6vh 4vw;
  margin: 3vh 1vw;
}



}


