#loadingcontainer{
    display: flex;
    align-items: center;
    justify-content: center;
height: 100vh;
width: 100vw;
position: fixed;
top: 0;
z-index: -1;
animation: 2.1s ease-in-out forwards 1;

}

@keyframes loading {
    0%{
        opacity: 1;
        z-index: -1;
    }
    15%{
        opacity: 1;
        z-index: 1000;
    }
85%{
        opacity: 1;
        z-index: 1000;
    }
    
    100%{
        opacity: 0;
        z-index: -1;

    }
    
}


#loadingcontainer > div{
    overflow: hidden;
height: 100%;
width: 10vw;
}
#loadingcontainer > div > div{
    transform: translateX(-100%);
    height: 100%;
    width: 100%;
    background-color: transparent;
    animation:  1.5s forwards ease-in-out 1;
}
@keyframes loadinge {
    0%{
        background-color: #0077b6;
        transform: translateX(-100%);
    }
    40%{
        background-color: #0077b6;
        transform: translateX(0%);
    }
    60%{
        background-color: #0077b6;
    transform: translateX(0%);
    }
    100%{
        background-color: #0077b6;
        transform: translateX(100%);
    }
}

