#projectcontainer{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
    transition: 0.5s;
    /* background-color: #786a6a; */
}

#projectheading{
  color: #282828;
  margin-bottom: 7vh;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  /* border-radius: 50px; */
  background-color: rgba(255, 255, 255, 0);
  z-index: 2;

}
#projectheading > span:nth-child(2){
  position: relative;
  font-size: 5rem;
  font-family: 'newyorkregular';

  color: #0077b6;
  font-weight: 600;
  text-rendering: optimizeLegibility;
}





#projectcontainer > div:nth-child(2){
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0vh 0vw;
}

.projects{
  position: relative;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  margin: 2vh 1vw;
  display: flex;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  transition: 0.3s;
  filter: brightness(100%) contrast(100%) saturate(0%) blur(0px) hue-rotate(0deg);
  
  
}
.projects:hover{
  filter: brightness(100%) contrast(100%) saturate(100%) blur(0px) hue-rotate(0deg);
  scale: 1.02;
}
.projects:hover > span{
transform: translateY(0vw);
opacity: 1;
}

.projects > span{
  border-radius: 4px;
  padding: 16vh 6vw;
  border-radius: 8px;

  color: white;
  text-align: center;
  transition: 0.3s;
  font-size: 1.2rem;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  background-color: #00000045;
opacity: 0;
transform: translateY(3vw);
}

#project1 {
  background-image: url('./Projectimages/ganesha/4.png');
}
#project2 {
  background-image: url('./Projectimages/nipman/Nipman\ Bawal-7.JPG');
  background-size: 593px;
}
#project3 {
  background-image: url('./Projectimages/raw/raw1.jpg');
}
#project4 {
  background-image: url('./Projectimages/fashiontech/Fashiontech-3.jpg');
  background-size: 993px;
}
#project5 {
  background-image: url('./Projectimages/dwarka/Westend\ Farms-1.jpg');
  background-size: 993px;
}



@media only screen and (max-width: 415px) {

  #projectcontainer {
    margin: 10vh 0vw
  }
  .projects{
  filter: brightness(100%) contrast(100%) saturate(100%) blur(0px) hue-rotate(0deg);

  }
  #projectheading > span:nth-child(2) {
    font-size: 4rem;
}
  #project1 > span{
    padding: 8vh 14vw;
  }
  #project2 > span{
    padding: 8vh 30vw;
  }
  #project3 > span{
    padding: 8vh 17vw
  }
  #project4 > span{
    padding: 8vh 21vw
  }
  #project5 > span{
    padding: 8vh 10vw
  }
}




 